<template>
	<div id='msg' class="warp">
		<!--头部导航 start-->
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<!-- <el-breadcrumb-item :to="{ path: '/message/mass'}">消息中心</el-breadcrumb-item> -->
				<el-breadcrumb-item :to="{ path:'/massgmenu/mass'}">群发消息</el-breadcrumb-item>
				<el-breadcrumb-item>发消息</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<!--头部导航 end-->
		<!--头部按钮组 start-->
		<div class="btn-group clearfix">
			<div class="left">
				<span class="send">今日还可以群发 {{total}} 条</span>
			</div>
			<div class="right">
				<button type="button" class="add" @click="getContent">发送</button>
				<button type="button" class="add" @click="showFlag">定时发送</button>
				<button type="button" class="add" @click='saveInfo'>存草稿</button>
				<router-link :to="{path:'/massgmenu/mass'}">
					<button type="button" class="add back">关闭</button>
				</router-link>
			</div>
		</div>
		<!--头部按钮组 end-->
		<!--消息内容 start-->
		<div class="edt-info clearfix">
			<div class=" left">
				<p class="text msg">消息标题</p>
				<p class="text msg">消息正文</p>
			</div>
			<input type="text" class="ipt" v-model='title' />
		</div>
		<!--消息内容 end-->
		<!--富文本编辑器 start-->
		<div class="editor" id='edt'></div>
		<!--富文本编辑器 end-->
		<!--定时发送模态框 start-->
		<div class="mask" v-if="showTiming" @click='close'></div>
		<div class="sends" v-if="showTiming">
			<div class="send-time">定时发送</div>
			<div class="edt-time">
				<span class="tm">选择发送时间</span>
				<el-date-picker v-model="value1" type="datetime" @change='contrast' placeholder="选择日期时间"></el-date-picker>
			</div>
			<div class="bottom">
				<button type="button" class="bl" @click="send">发送</button>
				<button type='button' class="adds" @click='close'>取消</button>
			</div>
		</div>
		<!--定时发送模态框 end-->
		<!--保存成功失败 Tips start-->
		<div class="tips" v-if='showTips'>
			<span class="info">{{msg}}</span>
		</div>
		<!--保存成功失败 Tips end-->
	</div>
</template>

<script>
	import Edtior from 'wangeditor';
	import moment from 'moment';
	import { Savemsg, getSysmsg } from '../../api';
	import { Send } from '@/config/cfg';
	import { Util} from '@/config/util';
	export default {
		data() {
			return {
				value1: '',  //选中的时间
				title: '',   //消息标题
				content: '', //消息内容
				edtior: null, //富文本编辑器
				total: null, //发送消息总数
				msgList: [], //消息数据
				count: '',   //草稿箱内容数量
				info: {},   //消息内容
				showTiming:false,  //显示定时发送模态框
				showTips: false, //显示Tips提示框
				msg:'' //提示框内容
			};
		},
		created() {
			this.getCount();
		},
		computed: {
			Send() {
				return Send;
			}
		},
		mounted() {
			this.getEdtior();
			this.getInfo();
		},
		methods: {
			//获取数据
			getInfo() {
				let _this = this;
				let newID = this.$route.query.newsID;
				getSysmsg({
					news_info: 1,
					news_id: newID,
				}, function(resp) {
					_this.info = resp.data.info || {};
					_this.title = _this.info.title;
					if(_this.info.content) {
						let content = `<p>${_this.info.content}</p>`;
						_this.content = _this.edtior.txt.html(content);
					}
				});
			},
			//获取可发送消息总数
			getCount() {
				let _this = this;
				getSysmsg({
					shop_news_num: 1
				}, function(resp) {
					_this.total = resp.data.num;
				});
			},
			//初始化富文本编辑器
			getEdtior() {
				this.edtior = new Edtior('#edt');
				this.edtior.create();
			},
			//获取富文本里面的内容
			getContent() {
				if(this.total == 0) {
					this.showTips = true;
					this.msg = '只能发送5条消息';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				this.content = this.edtior.txt.text();
				if(this.title == '' || this.content == '') {
					this.showTips = true;
					this.msg = '请将必填项填写完整再发送';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				this.total--;
				let newID = this.$route.query.newsID;
				let data = {
					news_save: 1,
					news_id: newID,
					title: this.title,
					content: this.content,
					is_draft: 0
				};
				Savemsg(data, this.alts);
			},
			//发送后的回调
			alts(resp){
				if(resp.ret === 0){
					this.showTips = true;
					this.msg = '发送成功';
					setTimeout(()=>{
						this.$router.go(-1);
					},1500);
				}else{
					this.showTips = true;
					this.msg = '发送失败';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
				}
			},
			//定时发送
			send(){
				if(this.total == 0) {
					this.showTips = true;
					this.msg = '只能发送5条消息';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				this.content = this.edtior.txt.text();
				if(this.title == '' || this.content == '') {
					this.showTips = true;
					this.msg = '请将必填项填写完整再发送';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}
				this.total--;
				let time = moment(this.value1).format('X');
				let newID = this.$route.query.newsID;
				let data = {
					news_save: 1,
					news_id: newID,
					title: this.title,
					content: this.content,
					is_draft: 0,
					send_time:time
				};
				Savemsg(data, this.alts);
			},
			//保存草稿
			saveInfo() {
				this.content = this.edtior.txt.text();
				let newID = this.$route.query.newsID;
				let data = {
					news_save: 1,
					title: this.title,
					content: this.content,
					is_draft: 1,
					news_id: newID
				};
				Savemsg(data);
				this.$router.push({
					path: "/message/drafts"
				});
			},
			//显示Tips框
			showFlag(){
				this.content = this.edtior.txt.text();
				if(this.title == '' || this.content == '') {
					this.showTips = true;
					this.msg = '请将必填项填写完整再发送';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return;
				}else{
					this.showTiming = true;
				}
				
			},
			//关闭弹框
			close(){
				this.showTiming = false;
				this.value1 = '';
			},
			//判断选择的时间小于当前时间
			contrast(){
				let times = parseInt(moment(this.value1).format('X'));
				let now = parseInt(moment(new Date()).format('X'));
				if(times < now){
					this.showTips = true;
					this.msg = '设置的定时时间已过期';
					setTimeout(()=>{
						this.showTips = false;
					},1500);
					return ;
				}
			}
		}
	};
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp {
		width: 100%;
		height: 844px;
		padding-bottom: 50px;
		background: #fff;
		position: relative;
		.nvs {
			background: #EFF3FA;
			height: 26px;
		}
		button {
				cursor: pointer;
			}
		.add {
				@include wh(100px, 34px);
				@include fc(14px, #4877E7);
				background: #fff;
				border: 1px solid #4877e7;
				margin-right: 10px;
				border-radius: 2px;
			}
		.bl{
				@include wh(160px,40px);
				@include fc(14px, #fff);
				background: #4877E7;
				border: 1px solid #4877e7;
				border-radius: 2px;
		}
		.adds{
				@include wh(160px, 40px);
				@include fc(14px, #4877E7);
				background: #fff;
				border: 1px solid #4877e7;
				border-radius: 2px;
				margin-left:60px;
		}
		.btn-group {
			padding: 20px 0;
			background: #fff;
			text-align: right;
			
			.right{
				font-size: 0;
			}
			.add {
				@include wh(100px, 34px);
				@include fc(14px, #4877E7);
				background: #fff;
				border: 1px solid #4877e7;
				margin-right: 10px;
				border-radius: 2px;
			}
			.left {
				margin-left:14px;
				@include fc(14px,#000);
			}
		}
		.edt-info {
			.left {
				width: 234px;
				text-align: right;
				margin-right: 14px;
			}
			.ipt {
				width: 70%;
				height: 34px;
				border: 1px solid #d9d9d9;
				margin-top: 10px;
			}
			.text {
				line-height: 54px;
			}
			.msg{
				position: relative;
				&:before{
					position: absolute;
					display: inline-block;
					content: '*';
					@include fc(18px,#E7487E);
					right:68px;
					top: 3px;
				}
			}
		}
		.editor {
			width: 70%;
			position: absolute;
			z-index: 10;
			left: 248px;
			top: 140px;
		}
		.mask{
			position: fixed;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			z-index: 1001;
			background:#000;
			opacity: 0.5;
		}
		.sends {
			position:fixed;
			z-index: 1002;
			left: 50%;
			top: 50%;
			margin-left: -270px;
			margin-top: -165px;
			@include wh(540px,330px);
			background: #fff;
			.send-time{
				height: 40px;
				line-height: 40px;
				background: #4877E7;
				text-align: center;
				@include fc(16px,#fff);
			}
			.edt-time{
				margin-top: 80px;
				margin-left: 101px;
				@include fc(14px,#333);
				.tm{
					margin-right: 8px;
				}
			}
			.bottom{
				font-size: 0;
				margin: 90px 80px 0;
			}
		}
		.tips {
			position:fixed;
			z-index: 1003;
			@include wh(360px, 100px);
			background: #000;
			opacity: 0.8;
			left: 50%;
			top: 50%;
			margin-left: -180px;
			margin-top: -50px;
			text-align: center;
			border-radius: 2px;
			.info {
				display: inline-block;
				@include fc(14px, #fff);
				padding-top: 40px;
				letter-spacing: 0.5;
			}
		}
		
	}
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#msg {
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
		div.w-e-text-container {
			width: 100% !important;
			height: 582px !important;
			z-index: 10 !important;
		}
		.el-date-editor.el-input{
			@include wh(240px,30px);
		}
		.el-input__inner{
			border-radius: 0;
		}
		
	}
</style>


